* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.flag-img {
  width: 320px;
}
.country-card {
  width: 320px;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
p {
  margin-top: 0;
  margin-bottom: 0;
  display: inline-block;
}

p {
  font-size: 20px;
}

.country-descr {
  display: flex;
  flex-direction: column;
}

.country-list {
  display: flex;
  flex-direction: column;
}
.country-list-item {
  display: flex;
  align-items: center;
}
.country-list-item:not(:last-child) {
  margin-bottom: 20px;
}
.list-flag-img {
  width: 70px;
  height: auto;
}
.country-list-name {
  margin-left: 10px;
}
